<script>
import axios from 'axios'
// let rdb = firebase.database()
import autophp from '@/plugins/autophp'
let sdb = new autophp()
let urlnya='https://gis.officialapp.website'
export default {
  data () {
    return {
      skill:[],
      subskill:[],
      activity:[]
    }
  },
  layout: 'portofolio',
  methods: {},
  mounted () {
    sdb.collection("app_portofolio_skill",false,urlnya).doc().select(`select * from app_portofolio_skill`).then(res => {
        this.skill=res;
        this.$forceUpdate();
    });
    
    sdb.collection("app_portofolio_subskill",false,urlnya).doc().select(`select * from app_portofolio_subskill`).then(res => {
        this.subskill=res;
        this.$forceUpdate();
    });
     sdb.collection("app_portofolio_activity",false,urlnya).doc().select(`select * from app_portofolio_activity`).then(res => {
        this.activity=res;
        this.$forceUpdate();
    });
    
  },
}
</script>
